body {
  background-color: #e59972;
  font-family: sans-serif;
}

input {
  margin: 1rem 3.5rem;
  outline: 0.2rem ridge #8d8d92;
  border-radius: 0.5rem;
  background-color: rgb(184, 89, 72);
  color: wheat;
}
::placeholder {
  color: wheat;
}

li {
  display: flex;
  justify-content: space-between;
  margin: 1.5rem 4rem 1.5rem 1.5rem;
  padding: 1rem;
  list-style: none;
  color: #584146;
  outline: 0.8rem ridge #31312f;
  border-radius: 1rem;
  transition: 0.3s;
}

li:hover {
  background-color: rgb(245, 188, 160);
  margin: 1.5rem 5.5rem 1.5rem 0rem;
}

.button-container {
  display: flex;
  justify-content: flex-end;
}

.edit {
  font-size: 1rem;
  background-color: #e59972;
  color: #e59972;
  border: #e59972;
  align-self: center;
  height: 1.3rem;
  width: 3rem;
  border-radius: 1rem 0rem 0rem 1rem;
}

li:hover .edit {
  background-color: rgb(101, 236, 236);
  color: #584146;
}

li:hover .edit:hover {
  background-color: rgb(29, 255, 255);
}
.delete {
  background-color: #e59972;
  color: #e59972;
  border: #e59972;
  align-self: center;
  height: 1.3rem;
  width: 3rem;
  border-radius: 0rem 1rem 1rem 0rem;
}

li:hover .delete {
  background-color: rgb(253, 81, 81);
  color: #584146;
}

li:hover .delete:hover {
  background-color: rgb(255, 47, 47);
}
